"use client"

import { useState, useMemo } from "react"
import {
  ChevronLeft,
  ChevronRight,
  Calendar as CalendarIcon,
  Clock,
  CheckCircle2,
  Circle
} from "lucide-react"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { ScrollArea } from "@/components/ui/scroll-area"
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetDescription
} from "@/components/ui/sheet"
import { cn } from "@/lib/utils"
import { useAppData } from "@/contexts/app-data-context"
import { aggregateEvents, type CalendarEvent } from "@/lib/calendar-service"

export default function CalendarPage() {
  const { activityLogs, customTrackers, customTrackerEntries } = useAppData()

  const [currentDate, setCurrentDate] = useState(new Date())
  const [selectedDate, setSelectedDate] = useState<Date>(new Date())
  const [isSheetOpen, setIsSheetOpen] = useState(false)

  // Aggregate events from all data sources
  const allEvents = useMemo(() => {
    const appData = { activityLogs, customTrackers, customTrackerEntries }
    return aggregateEvents(appData)
  }, [activityLogs, customTrackers, customTrackerEntries])

  // Filter events for selected day
  const selectedDayEvents = useMemo(() => {
    return allEvents.filter(event =>
      event.date.getDate() === selectedDate.getDate() &&
      event.date.getMonth() === selectedDate.getMonth() &&
      event.date.getFullYear() === selectedDate.getFullYear()
    )
  }, [allEvents, selectedDate])

  const daysInMonth = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0).getDate()
  const firstDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1).getDay()
  const monthName = currentDate.toLocaleString('default', { month: 'long' })
  const year = currentDate.getFullYear()

  const handlePrevMonth = () => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1, 1))
  const handleNextMonth = () => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 1))

  const handleDayClick = (day: Date) => {
    setSelectedDate(day)
    setIsSheetOpen(true)
  }

  // Generate days array
  const days = []
  for (let i = 0; i < firstDayOfMonth; i++) {
    days.push(null)
  }
  for (let i = 1; i <= daysInMonth; i++) {
    days.push(new Date(currentDate.getFullYear(), currentDate.getMonth(), i))
  }

  return (
    <div className="flex h-[calc(100vh-2rem)] gap-6 p-6 overflow-hidden">

      {/* LEFT COLUMN: CALENDAR GRID */}
      <div className="flex-1 flex flex-col bg-card border border-border rounded-2xl p-6 shadow-sm h-full">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h2 className="text-3xl font-display font-bold capitalize">{monthName}</h2>
            <p className="text-muted-foreground text-lg">{year}</p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="icon" onClick={handlePrevMonth}>
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Button variant="outline" size="icon" onClick={handleNextMonth}>
              <ChevronRight className="h-5 w-5" />
            </Button>
          </div>
        </div>

        {/* Days of week header */}
        <div className="grid grid-cols-7 mb-4">
          {['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'].map(day => (
            <div key={day} className="text-center font-medium text-muted-foreground text-sm uppercase tracking-wider py-2">
              {day}
            </div>
          ))}
        </div>

        {/* Calendar days */}
        <div className="grid grid-cols-7 gap-2 flex-1 auto-rows-fr">
          {days.map((day, index) => {
            if (!day) return <div key={`empty-${index}`} className="p-2" />

            const isSelected = day.toDateString() === selectedDate.toDateString()
            const isToday = day.toDateString() === new Date().toDateString()

            // Check if day has events
            const hasEvents = allEvents.some(e => e.date.toDateString() === day.toDateString())

            return (
              <button
                key={index}
                onClick={() => setSelectedDate(day)}
                className={cn(
                  "relative rounded-xl border p-2 flex flex-col items-start justify-between transition-all hover:border-primary/50 group",
                  isSelected ? "bg-primary/10 border-primary ring-1 ring-primary" : "bg-card/50 border-border/50",
                  isToday && !isSelected && "bg-accent/10 border-accent"
                )}
              >
                <span className={cn(
                  "text-sm font-bold w-7 h-7 flex items-center justify-center rounded-full",
                  isSelected ? "bg-primary text-primary-foreground" : "text-muted-foreground",
                  isToday && !isSelected && "text-accent"
                )}>
                  {day.getDate()}
                </span>

                {/* Event indicators */}
                {hasEvents && (
                  <div className="flex gap-1 mt-auto w-full px-1">
                    <div className="h-1.5 w-1.5 rounded-full bg-blue-500/70" />
                    <div className="h-1.5 w-1.5 rounded-full bg-orange-500/70" />
                  </div>
                )}
              </button>
            )
          })}
        </div>
      </div>

      {/* RIGHT COLUMN: TIMELINE */}
      <div className="w-[380px] bg-card border border-border rounded-2xl p-0 flex flex-col shadow-sm h-full overflow-hidden">
        {/* Day Header */}
        <div className="p-6 border-b border-border bg-muted/20">
          <h3 className="text-xl font-display font-bold">
            {selectedDate.toLocaleDateString('default', { weekday: 'long' })}
          </h3>
          <p className="text-muted-foreground">
            {selectedDate.toLocaleDateString('default', { day: 'numeric', month: 'long' })}
          </p>
        </div>

        <ScrollArea className="flex-1 p-6">
          {selectedDayEvents.length === 0 ? (
            // Empty state
            <div className="h-full flex flex-col items-center justify-center text-center opacity-50 min-h-[300px]">
              <CalendarIcon className="w-12 h-12 mb-4 text-muted-foreground/50" />
              <p className="font-medium">No activities planned</p>
              <p className="text-sm text-muted-foreground">Relax or add a task to get started.</p>
            </div>
          ) : (
            // Timeline
            <div className="space-y-6 relative">
              {/* Timeline line */}
              <div className="absolute left-[19px] top-2 bottom-2 w-[2px] bg-border/50 z-0" />

              {selectedDayEvents.map((event, idx) => (
                <div key={event.id} className="relative z-10 grid grid-cols-[40px_1fr] gap-4 group">

                  {/* Time/Icon */}
                  <div className="flex flex-col items-center pt-1">
                    <div className={cn(
                      "w-10 h-10 rounded-full border-4 border-card flex items-center justify-center shadow-sm",
                      event.color
                    )}>
                      {event.completed ? (
                        <CheckCircle2 className="w-4 h-4 text-white" />
                      ) : (
                        <Circle className="w-4 h-4 text-white fill-white/20" />
                      )}
                    </div>
                  </div>

                  {/* Event Card */}
                  <div className="bg-muted/30 border border-border/50 rounded-xl p-3 hover:bg-muted/60 transition-colors">
                    <div className="flex items-center justify-between mb-1">
                      <span className="text-xs font-bold uppercase tracking-wider text-muted-foreground flex items-center gap-1">
                        <Clock className="w-3 h-3" />
                        {event.startTime ? event.startTime : "All Day"}
                      </span>
                      <Badge variant="secondary" className="text-[10px] h-5 px-1.5 uppercase">
                        {event.type}
                      </Badge>
                    </div>
                    <h4 className={cn(
                      "font-semibold text-sm",
                      event.completed && "line-through text-muted-foreground"
                    )}>
                      {event.title}
                    </h4>
                  </div>
                </div>
              ))}
            </div>
          )}
        </ScrollArea>

        {/* Footer */}
        <div className="p-4 border-t border-border bg-muted/20">
          <Button className="w-full" variant="secondary">
            + Add Activity
          </Button>
        </div>
      </div>
    </div>
  )
}
